                  <div class="warnDiv">
                    <div>
                      <p @click="toHistoryTime(4)" style="cursor: pointer;">{{ EQUIPMENT_LIVE.ALARM_TIME }}</p>
                      <p>{{ stateData[3].value }}</p>
