                </div>
                <div class="divC">
                  <p style="color: aqua;">{{ EQUIPMENT_LIVE.STATE_TIME_CHART }}</p>
                  <div class="echartsDiv">
                    <div id="showEhcarts">
                    </div>
                    <span class="divC_title"  v-if="showDataInfo.isProductionCount===1">{{ $store.state.pageInfo.pageConfigInfo.yieldKey }}</span>
                    <span class="divC_num"  v-if="showDataInfo.isProductionCount===1">{{ cumulativeProduction }}</span>
                  </div>
