                <div class="divR">
                  <div class="freeDiv">
                    <div>
                      <p @click="toHistoryTime(2)" style="cursor: pointer;">{{ EQUIPMENT_LIVE.FREE_TIME }}</p>
                      <p>{{ stateData[1].value }}</p>
                    </div>
                  </div>
                  <div class="nolineDiv">
                    <div>
                      <p @click="toHistoryTime(3)" style="cursor: pointer;">{{ EQUIPMENT_LIVE.OFFLINE_TIME }}</p>
                      <p>{{ stateData[2].value }}</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </card-module>
        </div>
        <div class="left2">
          <card-module title="今日稼动率" :isExport="false" cardBg="cardBg_large" titleBg="cardTitle_large" width="100%" height="100%"
            titleWidth="70%" @exportExcel="exportEchart">
            <div style="width: 100%; height: 100%"  id="utilization"></div>
          </card-module>
        </div>
      </div>
      <div class="rightContent" ref="right_Content">
        <card-module :title="searchData.deviceName" :isExport="false" cardBg="cardBg_large" titleBg="cardTitle_large" width="100%" height="100%"
          titleWidth="50%" @exportExcel="exportTable">
          <VXETable :key="timers" :height="tableHeight" ref="xGrid" :tableName="''" :tableLoading="mainPage.tableLoading"
            :tableDatas="mainPage.data" :tableColumns="mainPage.columns" :highlightCurrentRow="true"
            :tablePage="mainPage.page" @sortChange="sortChange" @onTablePageChange="handleMainPageChange">
          </VXETable>
        </card-module>
      </div>
    </div>
    <el-dialog :class="['relaDevice', $store.state.dialogMarginClass]" :title="'状态履历'" :visible.sync="dialogVisible_time"
      width="70%" :modal-append-to-body="false" :before-close="handleClose_time">
      <VXETable id="myTable" :height="400" :tableName="''" :tableDatas="tableData_time" :tableColumns="tableColumn_time"
        :tablePage="tablePage_time" ref="xGrid"></VXETable>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose_time">{{ BTN_TEXT.CLOSE }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import VXETable from '@/components/common/Table.vue';
import $ from 'jquery';
import { KANBAN_CONFIG } from '@/utils/ajaxConfigure';
import CardModule from '@/components/common/kanban/CardModule';
import searchBar from './components/searchBar.vue';
import Header from '@/components/common/kanban/Header';
// import distributes from '@/views/kanban_bsb/distributes.vue';
