// 导入所需接口
import { fontSize, parseTime } from '@/utils/common';
import { deviceGetPageDeviceComplexByConditionAction } from '@/api/management';
import { GetOneDayDetailStateClockInfoOpen, getOneDayDetailStateClockOpen, getDeviceInfoOpen, getDeviceHoursEffTrendOpen, getAllGroupAndDeviceOpenApi } from '@/api/kanban';
import {
  STATUS_CODE,
  TITLE,
  BTN_TEXT,
  DEVICE_STATE,
  EQUIPMENT_LIVE,
  VARIABLE
  // TITLE
} from '@/utils/constantsFile';
import { showErrorNotify } from '@/utils/notify';
import { exportJson2Excel } from '@/utils/exportExcel';
// 导入通用方法
import { formatJson, dataFormatter } from '@/utils/publicFunction';
import {
  // getGroupId,
  getTodayFirstDay,
  getTodayLastDay,
  getSearchToday
} from '@/utils/publicFunction2';

export default {
  name: 'kanban',
  props: {
    deviceData: {
      type: Object,
      default: () => ({})
    }
  },
  components: {
    // 加载未审核组件
    VXETable,
    CardModule,
    // distributes
    searchBar,
    Header
  },
  data() {
    return {
      timers: '',
      showDataInfo: {},
      showData: {},
      tableHeight: 400,
      DEVICE_STATE,
      searchData: {
        groupValue: [1, 6, 7, '00000165_00000001'],
        date: new Date(),
        timeKeyEnum: 'DAY'
      },
      BTN_TEXT: BTN_TEXT,
      searchOptions: [
        {
          type: 'cascader',
          key: 'groupValue',
          options: []
        }
        // {
        //   type: 'daterange',
        //   key: 'date',
        //   options: [
        //     {
        //       label: '日',
        //       value: 'DAY'
        //     },
        //     {
        //       label: '周',
        //       value: 'WEEK'
        //     },
        //     {
        //       label: '月',
        //       value: 'MONTH'
        //     }
        //   ]
        // }
      ],
      EQUIPMENT_LIVE: EQUIPMENT_LIVE,
      cumulativeProduction: 0, // 今日产量
      stateDataTime: this.$utils.toDateString(new Date(), 'yyyy-MM-dd'),
      stateData: [
        {
          name: 0,
          value: EQUIPMENT_LIVE.NOTIME
        },
        {
          name: 0,
          value: EQUIPMENT_LIVE.NOTIME
        },
        {
          name: 0,
          value: EQUIPMENT_LIVE.NOTIME
        },
        {
          name: 0,
          value: EQUIPMENT_LIVE.NOTIME
        },
        {
          name: 0,
          value: EQUIPMENT_LIVE.NOTIME
        }
      ],
      mainPage: {
        show: true,
        page: {
          total: 0,
          currentPage: 1,
          pageSize: 20,
          align: 'right',
          pageSizes: [10, 20, 50, 100, 200, 500],
          layouts: ['Sizes', 'PrevPage', 'Number', 'NextPage', 'Total'],
          perfect: true
        },
        data: [],
        columns: [
          {
            field: 'productionTime',
            title: EQUIPMENT_LIVE.TIME,
            formatter: ['formatDate', 'yyyy-MM-dd HH:mm:ss'],
            width: '',
            align: 'center'
          },
          {
            field: 'deviceState',
            title: EQUIPMENT_LIVE.DEVICE_STATE,
            width: '',
            align: 'center',
            slots: {
              default: ({ row, column }) => {
                if (row.deviceState === 1) {
                  return DEVICE_STATE[1];
                } else if (row.deviceState === 2) {
                  return DEVICE_STATE[2];
                } else if (row.deviceState === 3) {
                  return DEVICE_STATE[3];
                } else if (row.deviceState === 4) {
                  return DEVICE_STATE[4];
                }
              }
            }
          },
          {
            field: 'todayProductionNum',
            title: EQUIPMENT_LIVE.ACCUMULATED_PROCESSING_QUANTITY,
            width: '',
            align: 'center'
          },
          // {
          //   field: 'todayWorkTime',
          //   title: EQUIPMENT_LIVE.WORKING_HOURS_OF_THE_DAY,
          //   width: '',
          //   formatter: 'timeStamp',
          //   align: 'center'
          // },
          {
            field: 'continuedTime',
            title: EQUIPMENT_LIVE.CONTINUOUS_WORKING_TIME,
            formatter: 'timeStamp',
            width: '',
            align: 'center'
          }
        ],
        tableLoading: false
      },
      dialogVisible_time: false,
      tableData_time: [],
      tablePage_time: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      },
      tableColumn_time: [
        {
          field: 'startTime',
          title: '开始时间',
          align: 'center'
        },
        {
          field: 'endTime',
          title: '结束时间',
          align: 'center'
        },
        {
          field: 'state',
          title: '状态',
          align: 'center',
          formatter: ({ cellValue }) => {
            if (cellValue === 1) {
              return '运行';
            } else if (cellValue === 2) {
              return '空闲';
            } else if (cellValue === 3) {
              return '离线';
            } else if (cellValue === 4) {
              return '告警';
            }
          }
        },
        {
          field: 'time',
          title: '持续时间',
          align: 'center',
          formatter: this.timeFunc
        }
      ],
      timeList: [],
      title: '',
      groupId: 0,
      timer: null,
      timer4: {},
      time: '',
      warnHtml: '',
      UtilizNum: 0,
      devList: [],
      devLbIndex: 0,
      // 设备分布数据
      productTaskData: [],
      // 生产任务 每行的展示的设备数
      rowDeviceNum: 10,
      current: 1,
      size: 6,
      current_w: 1,
      size_w: 4,
      kanbanConfig: { title: '', key: '', time: 15000 },
      electricList: []
    };
  },
  mounted() {
    this.$nextTick(() => {
      console.log(this.$refs.right_Content.clientHeight);
      this.tableHeight = this.$refs.right_Content.clientHeight - 45;
      this.mainPage.page.pageSize = Math.floor((this.tableHeight - 50) / 40);
    });
    document.documentElement.style.fontSize = fontSize() + 'px';
    window.onresize = () => {
      document.documentElement.style.fontSize = fontSize() + 'px';
    };
    // this.findSpecificDevice();
    this.timer = setInterval(() => {
      this.loadData();
    }, this.kanbanConfig.time || 15000);
  },
  destroyed() {
    this.timer && clearInterval(this.timer);
  },
  created() {
    if (this.$store.state.deviceIsJump === true) {
      this.deviceData = this.$store.state.deviceJump;
    }
    var that = this;
    if (KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)]) {
      that.kanbanConfig = KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)];
    }
    if (this.getUrlKeyMain('tvNo', window.location.href) !== null) {
      this.title = that.kanbanConfig.group[this.getUrlKeyMain('tvNo', window.location.href)].title;
      this.groupId = that.kanbanConfig.group[this.getUrlKeyMain('tvNo', window.location.href)].id;
    } else {
      this.title = that.kanbanConfig.title;
    }
    this.getGroupLineDevice();
  },
  methods: {
    loadData() {
      this.getDeviceShowInfo();
      this.getState();
      this.findList();
      this.getEfficiencyData();
    },
    // 导出图表数据
    exportEchart() {
      const multiHeader = [];
      exportJson2Excel({
        multiHeader,
        header: this.xData.reverse(),
        data: [this.seriesData.reverse()],
        filename: '告警时长TOP5',
        merges: []
      });
    },
    // 获取 车间 —— 产线 —— 设备
    async getGroupLineDevice() {
      try {
        var param = {
          isProductionDevice: 1
        };
        const res = await getAllGroupAndDeviceOpenApi(param, { secretKey: this.kanbanConfig.key });
        for (var i = 0; i < res.rows.length; i++) {
          if (res.rows[i].code) {
            res.rows[i].id = res.rows[i].code;
          }
        }
        const data = dataFormatter(res.rows);
        this.searchOptions[0].options = data;
        const deviceCode = this.searchData.groupValue[this.searchData.groupValue.length - 1];
        this.searchData.deviceName = res.rows.find(item => item.id === deviceCode).label;
        // console.log(device);
        this.loadData();
      } catch (error) {
        console.log(error);
      }
    },
    // 查询具体设备
    async findSpecificDevice() {
      try {
        const params = {
          startTime: getTodayFirstDay(),
          endTime: getTodayLastDay(),
          current: 1,
          size: 99999999
        };
        var conditionJson = {
          conditionJson1: [
            { column: 'isCollect', type: 'eq', ptype: 'string', value: 1, operator: 1 }
          ],
          operatorGroup: 1,
          conditionJson2: []
        };
        const res = await deviceGetPageDeviceComplexByConditionAction(params, conditionJson);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.searchOptions[0].options = res.rows.map(item => ({
            label: item.deviceName,
            value: item.deviceCode
          }));
          const params = new URLSearchParams(location.search);
          if (params.has('deviceCode')) {
            this.searchData.groupValue = params.get('deviceCode');
          } else {
            this.searchData.groupValue = res.rows[0].deviceCode;
            this.searchData.deviceName = res.rows[0].deviceName;
          }
          this.loadData();
        } else {
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    async findList() {
      try {
        this.tableLoadings = true;
        const res = await GetOneDayDetailStateClockInfoOpen({
          size: this.mainPage.page.pageSize,
          current: this.mainPage.page.currentPage,
          deviceCode: this.searchData.groupValue[this.searchData.groupValue.length - 1],
          time: this.searchData.date
        }, {
          secretKey: this.kanbanConfig.key
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          if (this.mainPage.page.pageSize * this.mainPage.page.currentPage >= res.total) {
            this.mainPage.page.currentPage = 1;
          } else {
            this.mainPage.page.currentPage++;
          }
          var tabData = JSON.parse(JSON.stringify(res.rows));
          this.mainPage.data = tabData.reverse();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    // 稼动率报表
    showEcharts3(xAxisData, seriesData) {
      // var dom = 800;
      // var barWidth = dom / 20;
      // const sideData = data.map(item => item * 1.05);
      // myChart = this.$echarts.init(document.getElementById('showEhcarts'));
      const lineChart = this.$echarts.init(document.getElementById('utilization'));
      const option = {
        // title: {
        //   text: '',
        //   textStyle: {
        //     color: '#fff',
        //     fontSize: 10
        //   },
        //   x: 'center',
        //   padding: [3, 0],
        //   left: '80%'
        // },
        grid: {
          top: '10%',
          left: '4%',
          right: '5%',
          bottom: '10%',
          containLabel: true
        },
        tooltip: {
          trigger: 'axis',
          formatter: param => {
            // console.log(123, param);
            return param[0].axisValue + ' : ' + param[0].data + '%';
          },
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        xAxis: {
          data: xAxisData,
          // 坐标轴
          axisLine: {
            lineStyle: {
              color: '#3eb2e8'
            }
          },
          // 坐标值标注
          axisLabel: {
            show: true,
            textStyle: {
              color: '#fff'
            }
          }
        },
        yAxis: {
          // 坐标轴
          axisLine: {
            show: false
          },
          // 坐标值标注
          axisLabel: {
            show: true,
            textStyle: {
              color: '#fff'
            }
          },
          // 分格线
          splitLine: {
            lineStyle: {
              color: '#4784e8'
            }
          }
        },
        series: [
          {
            type: 'bar',
            // barWidth: barWidth,
            itemStyle: {
              normal: {
                color: '#e2cd17'
              }
            },
            label: {
              show: false,
              // position: [barWidth / 2, -(barWidth - 10)],
              color: '#ffffff',
              fontSize: 14,
              fontStyle: 'bold',
              align: 'center',
              formatter: params => {
                return params.value + '%';
              }
            },
            data: seriesData
          }
        ],
        animationDuration: 0,
        animationEasing: 'cubicInOut'
      };
      lineChart.setOption(option, true);
      // window.addEventListener('resize', () => {
      //   lineChart.resize();
      // });
    },
    // 获取设备基础数据
    async getDeviceShowInfo() {
      try {
        const res = await getDeviceInfoOpen({
          deviceCode: this.searchData.groupValue[this.searchData.groupValue.length - 1]
        }, {
          secretKey: this.kanbanConfig.key
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.showDataInfo = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    // 获取效率趋势图数据
    async getEfficiencyData() {
      try {
        const endTime = parseTime(this.searchData.date, '{y}-{m}-{d}') + ' 23:59:59';
        const startTime = parseTime(this.searchData.date, '{y}-{m}-{d}') + ' 00:00:00';
        const data = await getDeviceHoursEffTrendOpen(
          {
            deviceCode: this.searchData.groupValue[this.searchData.groupValue.length - 1],
            startTime,
            endTime
          },
          {
            secretKey: this.kanbanConfig.key
          }
        );

        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          const key = data?.rows.map(item => item.time?.substr(11, 5));
          const value = data?.rows.map(item => item.value);
          this.showEcharts3(key, value);
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      }
    },
    exportTable() {
      const multiHeader = [];
      const header = this.mainPage.columns.map(item => {
        if (item.visible !== true) {
          item.disabled = true;
        } else {
          return item.title;
        }
      });
      const filterVal = this.mainPage.columns.map(item => {
        if (item.visible !== true) {
          item.disabled = true;
        } else {
          return item.field;
        }
      });
      const data = formatJson(filterVal, this.mainPage.data);
      exportJson2Excel({
        multiHeader,
        header,
        data,
        filename: '产能明细',
        merges: []
      });
    },
    search() {
      this.getDeviceShowInfo();
      this.getState();
      this.findList();
      this.getEfficiencyData();
    },
    sortChange(property, order) {
      this.orderByData = {};
      this.orderByData[property] = order;
      this.handleMainPageChange(1, this.mainPage.page.pageSize);
    },
    handleMainPageChange(currentPage, pageSize) {
      this.mainPage.page.currentPage = currentPage;
      this.mainPage.page.pageSize = pageSize;
      // this.findList();
    },
    // 显示状态履历模态框
    toHistoryTime(param) {
      this.dialogVisible_time = true;
      var arr = [];
      for (var i = 0; i < this.timeList.length; i++) {
        if (this.timeList[i].state === param) {
          arr.push(this.timeList[i]);
        }
      }
      this.tableData_time = arr;
      this.tablePage_time.total = arr.length;
    },
    handleClose_time() {
      this.dialogVisible_time = false;
      // this.hackReset = false;
    },
    /**
        * @description: 获取设备24小时状态
        * @param obj ztree对象
        * @return: 无
        *@author: CH
        */
    async getState() {
      try {
        // this.tableLoadings = true;
        const res = await getOneDayDetailStateClockOpen({
          deviceCode: this.searchData.groupValue[this.searchData.groupValue.length - 1],
          time: this.searchData.date
        }, {
          secretKey: this.kanbanConfig.key
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // this.showData = res.rows;
          if (res.rows.cumulativeProduction) {
            // this.cumulativeProduction = res.rows.cumulativeProduction;
            if (getSearchToday(0) === this.stateDataTime) {
              if (res.rows.currentShiftName) {
                this.cumulativeProduction = res.rows.currentValue;
              } else {
                this.cumulativeProduction = res.rows.cumulativeProduction;
              }
            } else {
              this.cumulativeProduction = res.rows.cumulativeProduction;
            }
          } else {
            this.cumulativeProduction = 0;
          }
          // var color = [];
          var echartsData = [];
          this.timeList = res.rows.timeList;
          for (var i in res.rows.timeList) {
            // echartsData[i].name = res.rows[i].state;
            // echartsData[i].value = res.rows[i].time;
            if (res.rows.runTimeSum) {
              this.stateData[0].value = this.timeStamp(res.rows.runTimeSum);
            } else {
              this.stateData[0].value = '0分钟';
            }
            if (res.rows.idleTimeSum) {
              this.stateData[1].value = this.timeStamp(res.rows.idleTimeSum);
            } else {
              this.stateData[1].value = '0分钟';
            }
            if (res.rows.offlineTimeSum) {
              this.stateData[2].value = this.timeStamp(res.rows.offlineTimeSum);
            } else {
              this.stateData[2].value = '0分钟';
            }
            if (res.rows.warnTimeSum) {
              this.stateData[3].value = this.timeStamp(res.rows.warnTimeSum);
            } else {
              this.stateData[3].value = '0分钟';
            }
            if (res.rows.stopTimeSum) {
              this.stateData[4].value = this.timeStamp(res.rows.stopTimeSum);
            } else {
              this.stateData[4].value = '0分钟';
            }
            if (res.rows.timeList[i].state === 0) {
              // color.push('#FFFFFF');
              echartsData.push({
                name: res.rows.timeList[i].state,
                value: res.rows.timeList[i].time,
                itemStyle: { color: '#FFFFFF' }
              });
            }
            if (res.rows.timeList[i].state === 1) {
              // this.stateData[0].name = res.rows[i].state;
              // this.stateData[0].value = this.timeStamp(res.rows[i].time);
              // color.push('#027cf8');
              echartsData.push({
                name: res.rows.timeList[i].state,
                value: res.rows.timeList[i].time,
                itemStyle: { color: '#027cf8' }
              });
            }
            if (res.rows.timeList[i].state === 2) {
              echartsData.push({
                name: res.rows.timeList[i].state,
                value: res.rows.timeList[i].time,
                itemStyle: { color: '#fed016' }
              });
            }
            if (res.rows.timeList[i].state === 3) {
              echartsData.push({
                name: res.rows.timeList[i].state,
                value: res.rows.timeList[i].time,
                itemStyle: { color: '#5b6069' }
              });
            }
            if (res.rows.timeList[i].state === 4) {
              echartsData.push({
                name: res.rows.timeList[i].state,
                value: res.rows.timeList[i].time,
                itemStyle: { color: '#c84747' }
              });
            }
            if (res.rows.timeList[i].state === 5) {
              echartsData.push({
                name: res.rows.timeList[i].state,
                value: res.rows.timeList[i].time,
                itemStyle: { color: '#ff6600' }
              });
            }
          }
          this.showEcharts_State(echartsData);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
        * @description: 处理时间显示
        * @param obj ztree对象
        * @return: 无
        *@author: CH
        */
    timeStamp(StatusMinute) {
      var day = parseInt(StatusMinute / 60 / 24);
      var hour = parseInt((StatusMinute / 60) % 24);
      var min = parseInt(StatusMinute % 60);
      var value = '';
      if (day === 1) {
        value = '24' + VARIABLE.HOUR_NAME + '0' + VARIABLE.MINUTE_NAME;
      }
      if (hour > 0) {
        value += hour + VARIABLE.HOUR_NAME;
      }
      if (min > 0) {
        value += parseFloat(min) + VARIABLE.MINUTE_NAME;
      }

      if (StatusMinute % 1 > 0) {
        value += parseInt((StatusMinute % 1) * 60) + '' + '秒';
      }

      return value;
    },
    /**
        * @description:展示24小时状态图
        * @param {type}
        * @return:
        * @author: CH
        */
    showEcharts_State(echartsData) {
      var myChart = this.$echarts.init(document.getElementById('showEhcarts'));
      // 绘制图表
      myChart.setOption({
        series: [
          {
            name: '',
            type: 'pie',
            radius: ['60%', '80%'],
            hoverAnimation: false,
            // avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: 'center'
              },
              emphasis: {
                show: false
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            // color: color,
            data: echartsData
          }
        ]
      });
    },
    // 名称格式化
    nameFormat(value, cutter, newline) {
      var s = value;
      var num0 = cutter;
      var num1 = cutter;
      var flag = 1; // 换行标记
      if (s.length > 2 * cutter + 1) {
        var rs = s.split('|');
        if (rs.length > 1) {
          if (rs[0].length < cutter) {
            num0 = rs[0].length;
            num1 = 2 * cutter - rs[0].length;
          }
          if (rs[1].length < cutter) {
            num0 = 2 * cutter - rs[1].length;
            num1 = rs[1].length;
          }
          rs[0] = rs[0].substring(0, num0);
          rs[1] = rs[1].substring(0, num1);
          if (rs != null) {
            s = rs.join('|');
          }
        }
      } else {
        // eslint-disable-next-line no-redeclare
        var rs = s.split('|');
        if (rs.length > 1) {
          if (rs[0].length < cutter && rs[1].length < cutter) {
            flag = 0;
          }
          if (rs != null) {
            s = rs.join('|');
          }
        }
      }
      if (newline === 1) {
        if (flag === 1) {
          s = s.substring(0, s.length / 2) + '\n' + s.substring(s.length / 2);
        } else if (flag === 0) {
          s = s.split('|').join('\n');
        }
      }
      return s;
    },
    // 看板时间
    tick() {
      var objD = new Date();
      var yy = objD.getYear();
      if (yy < 1900) yy = yy + 1900;
      var MM = objD.getMonth() + 1;
      if (MM < 10) MM = '0' + MM;
      var dd = objD.getDate();
      if (dd < 10) dd = '0' + dd;
      var hh = objD.getHours();
      if (hh < 10) hh = '0' + hh;
      var mm = objD.getMinutes();
      if (mm < 10) mm = '0' + mm;
      var ss = objD.getSeconds();
      if (ss < 10) ss = '0' + ss;
      this.time = yy + ' 年 ' + MM + ' 月 ' + dd + ' 日 ' + hh + ' : ' + mm + ' : ' + ss;
    },
    // 看板时间
    getDataTime() {
      // $.ajax({
      //   type: 'GET',
      //   url: 'http://oa.junhepumps.com:8081/idevcloudwebsub48/junheProductionTask/getPageDeviceRealInformation.action?pageNum=1&pageSize=74',
      //   data: {},
      //   dataType: 'jsonp',
      //   success: function(data) {
      //     console.log('请求成功', data);
      //   },
      //   error: function(data) {
      //     console.log('请求失败', data);
      //   },
      //   crossDomain: true,
      //   xhrFields: { withCredentials: true }
      // });
      this.dataTime = new Date($.ajax({ type: 'HEAD', async: false }).getResponseHeader('Date')).getTime();
      console.log('时间', $.ajax({ type: 'HEAD', async: false }).getResponseHeader('Date'));
      this.toGetTime();
    },
    toGetTime() {
      setInterval(() => {
        var date = new Date(this.dataTime);
        var y = date.getFullYear();
        var M = date.getMonth() < 9 ? '0' + (date.getMonth() + 1) : (date.getMonth() + 1);
        var d = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
        var h = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
        var m = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();
        var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
        this.time = y + ' 年 ' + M + ' 月 ' + d + ' 日   ' + h + ':' + m + ':' + s;
        this.dataTime += 1000;
      }, 1000);
    },
    showLogo() {
      return 'logo_' + this.kanbanConfig.logo;
    },
    showLogo2() {
      if (this.getUrlKeyMain('cid', window.location.href) === '161') {
        return 'logo_lt2';
      } else {
        return '';
      }
    },
    showLogo3() {
      if (this.getUrlKeyMain('cid', window.location.href) === '161') {
        return 'logo_ry2';
      } else {
        return '';
      }
    },
    getUrlKeyMain(name, url) {
      // eslint-disable-next-line no-sparse-arrays
      return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(url) || [, ''])[1].replace(/\+/g, '%20')) || null;
    },
    formatDateThree(time) {
      var date = '';
      var D = '';
      date = new Date(time);
      var M = date.getMonth() < 9 ? '0' + (date.getMonth() + 1) : (date.getMonth() + 1);
      var d = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
      D = M + '-' + d;
      return D;
    },
    limitMoveNum() {
      let num = 0;
      this.productTaskData.forEach(item => {
        num += item.length;
      });
      return num > 2 ? 1 : 3;
    }
  }

};
</script>

